import Vue from 'vue';
import VueRouter from 'vue-router';

const CalcForm = resolve => {
  require.ensure(["./components/CalcForm"], () => {
    resolve(
      require("./components/CalcForm")
    )
  })
};
const CompaniesList = resolve => {
  require.ensure(["./components/CompaniesList"], () => {
    resolve(
      require("./components/CompaniesList")
    )
  })
};
const Checkout = resolve => {
  require.ensure(["./components/Checkout"], () => {
    resolve(
      require("./components/Checkout")
    )
  })
};
const NotFound = resolve => {
  require.ensure(["./components/NotFound"], () => {
    resolve(
      require("./components/NotFound")
    )
  })
};
Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Calc',
    component: CalcForm
  },
  {
    path: '/variants',
    name: 'Variants',
    component: CompaniesList
  },
  {
    path: '/checkout',
    name: 'Checkout',
    component: Checkout
  },
  /*{
    path: '/404',
    name: '404',
    component: NotFound,
  }, {
    path: '*',
    redirect: '/404'
  }*/
];

export const router = new VueRouter({
  routes,
  mode: 'history'
});



